import { useState } from "react";
import { MainContainer } from "../main/main_container";
import styles from "./login.module.scss";

export const Signup = () => {

  const [info, set_info] = useState("");
  
  const handle_submit = async () => {
    const login = (document.querySelector("#username") as HTMLInputElement).value;
    const password = (document.querySelector("#password") as HTMLInputElement).value;
    const confirm = (document.querySelector("#confirm") as HTMLInputElement).value;
    try {
      const get_credential = await fetch(`https://world-countries-aad9f-default-rtdb.firebaseio.com/${login}.json`)
      const credential = await get_credential.json();
      if(credential){
        set_info(`The username ${login} is already taken.`);
      } else {
        fetch(`https://world-countries-aad9f-default-rtdb.firebaseio.com/${login}.json`, {
          method: `POST`,
          body: JSON.stringify({
            login: login,
            password: password
          })
        });
      }
    } catch(e) {
      set_info(`Your login didn't go through`);
    }
  }

  return (
    <MainContainer header="Sign up" >
      <section className="login"> 
        <form className={styles.form} action="/profile" method="post">
          {info === "" ? "" : <h2>{info}</h2>}
          <label htmlFor="username">Username</label>
          <input type="text" name="username" id="username" />
          <label htmlFor="password">Password</label>
          <input type="password" name="password" id="password" />
          <label htmlFor="confirm">Confirm</label>
          <input type="confirm" name="confirm" id="confirm" />
          <button 
            className={styles.login} 
            type="button"
            onClick={handle_submit}
          >
            Sign Up
          </button>
        </form>
      </section>
    </MainContainer>
  )
}

